.appstore-block-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
  white-space: normal;
  --f7-block-title-font-size: 21px;
  --f7-block-title-font-weight: bold;
  --f7-block-title-line-height: 1.4;
}
.appstore-block-title:before {
  content: '';
  position: absolute;
  background-color: var(--f7-block-strong-border-color);
  display: block;
  z-index: 15;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  height: 1px;
  width: 100%;
  transform-origin: 50% 0%;
  transform: scaleY(calc(1 / var(--f7-device-pixel-ratio)));
}
.appstore-block-title > .link {
  font-size: 17px;
  font-weight: normal;
  flex-shrink: 0;
}
